.consultation {
  display: grid;
  grid-template-columns: repeat(2, 413px);
  justify-content: space-between;

  &__text {
    font-size: 16px;
    line-height: 27px;
    color: #3e3e3e;
  }

  &__heading {
    font-size: 24px;
    line-height: 30px;
    color: #0075ff;
    margin-bottom: 16px;
  }

}
