.testimonials {
  display: grid;
  grid-template-columns: repeat(3, 240px);
  justify-content: space-between;
}

.person {
  text-align: center;

  &__photo {
    border-radius: 50%;
  }

  &__name {
    font-size: 24px;
    line-height: 30px;
    color: #0075ff;
    margin-bottom: 4px;
  }

  &__position {
    font-size: 14px;
    line-height: 27px;
    margin-bottom: 13px;
    color: #9fadc2;
  }

  &__post {
    color: #3e3e3e;
  }

}
